.form .input {
    width: calc(100% - 9px);
    height: 40px;
    outline: 0px solid red;
}

.form .input:focus {
    background-color: lightblue;
  }

.field{
    margin-bottom: 10px;
}

.field label {
    font-weight: bold;
}

.form .checkbox{
    width: 20px;
    height: 20px;
}

.button{
    width: 80px;
    height: 30px;
    background-color: #008CBA;
    color: white;
}


.captcha {
    text-align: center;
}

.captchaImg {
    height: 100px;
    width: 250px;
    outline: 0px solid red;
}


.captcha input {
    width: 200px;
    height: 20px;
}


